import {tab,address,recentlyBrowse,city} from 'js/cache'
const state = {
    category: tab.getTab(),   //
    goodsDetails: {},   // 
    userName: '',       // 
    addressInfo: address.getAddress(),    //  
    browse: recentlyBrowse.getBrowse(), // 
    shopOrderList: [],      // vuex
    temporaryAddress: '',   // 
    city: city.getCity(),
}

export default state


